/**
 * Slug size.
 */
export enum SLUG_SIZE {
  /**
   * Mini size.
   */
  MINI = 'mini',

  /**
   * Extra extra small size.
   */
  EXTRA_EXTRA_SMALL = '2xs',

  /**
   * Extra small size.
   */
  EXTRA_SMALL = 'xs',

  /**
   * Small size.
   */
  SMALL = 'sm',

  /**
   * Medium size.
   */
  MEDIUM = 'md',

  /**
   * Large size.
   */
  LARGE = 'lg',

  /**
   * Extra large size.
   */
  EXTRA_LARGE = 'xl',
}

/**
 * Slug kind.
 */
export enum SLUG_KIND {
  /**
   * Default kind.
   */
  DEFAULT = '',

  /**
   * Inline kind.
   */
  INLINE = 'inline',
}
